@import url(https://fonts.googleapis.com/css?family=Julius+Sans+One|Josefin+Slab|Open+Sans:300|Cabin+Sketch|Fredericka+the+Great|Lobster|Quicksand|Fascinate|Bahiana&display=swap);
.home {
  overflow: hidden;
  width: 100%;
  font-family: "Fredericka the Great", cursive;
}
.mail {
  display: none;
}
.full {
  height: 100vh;
  display: grid;
  grid-template-rows: auto repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 15px;
}
.full > a:not(.welcome) {
  font-family: "Julius Sans One", sans-serif;
  font-size: 2.3em;
  color: #ff7214;
  z-index: 2;
  width: 50%;
  place-self: center;
}
.full > a:hover {
  color: yellow;
  transition: color 2s;
}
.aboutme {
  grid-area: 2 / 1 / 2 / 2;
}
.work {
  grid-area: 3 / 1 / 3 / 2;
}
.contactNav {
  grid-area: 4 / 1 / 4 / 2;
}
.iconbar {
  grid-area: 1 / 1 / 1 / 4;
  display: -webkit-flex;
  display: flex;
  font-family: "Josefin Slab", serif;
  background-color: white;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  color: #ff7214;
  padding-left: 5%;
}
.iconbar i {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.5em;
  border: 2px dotted transparent;
  border-radius: 50%;
  height: 80px;
  width: 80px;
  margin: 10px;
  z-index: 2;
  color: #ff7214;
}
.iconbar i:hover {
  color: rgba(255, 0, 180, 0.5);
}
.iconbar i:hover {
  border-top-color: navy;
  border-right-color: navy;
  border-bottom-color: navy;
  border-left-color: navy;
  transition: border-top-color 0.15s linear,
    border-right-color 0.15s linear 0.1s, border-bottom-color 0.15s linear 0.2s,
    border-left-color 0.15s linear 0.3s;
}
.welcome {
  grid-area: 1 / 1 / 5/ 4;
  background-image: url(/portfolio/static/media/rose-egg.8b627521.svg);
  background-size: cover;
  background-position: right;
  background-repeat: no-repeat;
  z-index: 1;
}
.welcomeP {
  grid-area: 2 / 3 / 5/ 4;
  z-index: 2;
  font-family: "Josefin Slab", serif;
  color: black;
  font-weight: bolder;
  text-transform: uppercase;
  font-size: 3rem;
  text-align: right;
  border-top: 10px dotted orangered;
  padding: 7rem 20px 0 0;
  margin: 20px;
  line-height: 1.1em;
  position: absolute;
  top: 1rem;
  right: 0;
  bottom: 0;
  width: 50%;
  line-height: 2rem;
}

#about{
  display: flow-root;
}
.aboutbox article:first-child {
  border-bottom: 1px solid white;
  /* text-align: center; */
  padding-bottom: 50px; 
}
.aboutbox>article:not(.bio){
  padding-top: 50px;
}
.bio .date{
float:left;
}
.skills .date{
float:right;
}
.skills>p span{
 color:palevioletred;
  font-size: larger;
  font-weight: bolder;
  font-family: 'Julius Sans One', sans-serif;
}
.date span {
  font-family: "Lobster", cursive;
  font-size: 3em;
  line-height: 90px;
  color: #fce181;
}
.date {
  border: 2px white dotted;
  -webkit-shape-outside: circle(95px);
          shape-outside: circle(95px);
  width: 90px;
  height: 90px;
  border-radius: 90px;
  text-align:center;
  margin: 50px;
  /* background-color: gold; */
}


.row {
  margin: 8px -16px;
}
.row:after {
  content: "";
  display: table;
  clear: both;
}
.row,
.row > .column {
  padding: 8px;
}
.column {
  float: left;
  width: 50%;
}
.projectbox {
  position: relative;
  padding: 10px;
  text-align: center;
}
.projectbox img {
  background-color: white;
  width: 100%;
  transition: opacity ease-out 250ms;
  opacity: 1;
}
.projectbox:hover img {
  opacity: 0.1;
}
.projectbox::before {
  content: "";
  position: absolute;
  top: 1.25em;
  bottom: 1.25em;
  left: 1.25em;
  right: 1.25em;
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
  transition: -webkit-transform ease-out 250ms;
  transition: transform ease-out 250ms;
  transition: transform ease-out 250ms, -webkit-transform ease-out 250ms;
  border-bottom: 1px solid white;
}
.projectbox::after {
  content: "";
  position: absolute;
  top: 1.25em;
  bottom: 1.25em;
  left: 1.25em;
  right: 1.25em;
  -webkit-transform: scale(1, 0);
          transform: scale(1, 0);
  transition: -webkit-transform ease-out 250ms;
  transition: transform ease-out 250ms;
  transition: transform ease-out 250ms, -webkit-transform ease-out 250ms;
  border-left: 1px solid white;
}
.projectbox:hover::before {
  -webkit-transform: scale(1.05, 1);
          transform: scale(1.05, 1);
}
.projectbox:hover::after {
  -webkit-transform: scale(1, 1.05);
          transform: scale(1, 1.05);
}
.projectbox ul {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 0;
  z-index: 1;
  transition: opacity ease-out 250ms;
}
.projectbox:hover ul {
  opacity: 1;
}
.projectbox li p {
  text-align: center;
  font-size: 0.5rem !important;
}
h2 {
  font-size: 1.5rem;
  padding-bottom: 15px;
  font-family: "Lobster", cursive;
  white-space: wrap;
  color: #ff7214;
  text-align: center;
}
.projectbox li:last-child {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.button {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border: 2px dotted white;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  margin: 10px;
}
.button i {
  font-size: 1.5em;
  color: #fce181;
}
ul span {
  display: none;
}

.contact {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
}
form,
.collab {
  -webkit-flex-basis: 50%;
          flex-basis: 50%;
}
input,
textarea,
button {
  width: 100%;
  border: 2px solid transparent;
  margin: 5px;
  padding: 5px;
  background-color: transparent;
  color: gold;
  font-size: 1em;
  resize: vertical;
}
textarea {
  height: 20vw;
}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
  color: white;
  text-align: left;
}
input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  color: white;
  text-align: left;
}
input::placeholder,
textarea::placeholder {
  color: white;
  text-align: left;
}
.contact ul li {
  border-bottom: 1px solid gold;
}
button[type="submit"] {
  text-align: left;
}
button[type="submit"] i {
  font-size: 2em;
  color: white;
}
button[type="submit"] i:hover {
  color: gold;
  transition: color 1s;
}
.collab {
  -webkit-align-self: center;
          align-self: center;
  text-align: center;
  color: #ff7214;
  text-shadow: 5px 5px 0 rgba(255, 0, 180, 0.5);
  font-size: 3rem;
  font-weight: bolder;
  font-family: "Julius Sans One", sans-serif;
}
#next-line-intent {
  text-indent: 2em;
  font-size: 1em;
  text-shadow: 5px 5px 0 rgba(255, 0, 180, 0.5);
}

.navTop {
    display: -webkit-flex;
    display: flex;
    place-content: space-evenly;
    padding:50px;
    font-family: 'Josefin Slab', serif;
    font-weight: bolder;
  }
 .text{
   color: gold;
  }
  .navTop .text:hover{
    color: #333;
    transition: color 2s;
  }
  .navTop i{
  font-size:2em;
  }
  .navTop .icon{
    display: none;
  }

@media screen and (max-width: 900px) {
  .column {
    width: 50%;
  }
}
@media screen and (max-width: 768px) {
  .home {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  .full {
    grid-template-columns: 1fr 30%;
    grid-gap: 0;
    font-family: "Josefin Slab", serif;
    outline: 20px solid transparent;
  }
  .full > a:not(.welcome) {
    width: auto;
  }
  .welcomeP,
  .welcome {
    display: none;
  }
  .iconbar {
    padding: 0;
    height: 100%;
    grid-row: 1/5;
    grid-column: 2;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-content: space-evenly;
            align-content: space-evenly;
    background-image: url(/portfolio/static/media/rose-egg.8b627521.svg);
    background-position: center center;
    background-size: cover;
  }
  .iconbar i {
    font-size: 3em;
  }
  .mail {
    display: block;
    grid-area: 4 / 1 / 4 / 2;
  }
  /* about  */

  .aboutbox-article {
    display: block;
    margin: 0;
  }
  .aboutbox p {
    padding: 0 10px;
    margin: 0;
  }
  .date {
    display: none;
  }
  .projectbox {
    height: auto;
  }
  .projectbox:hover img {
    opacity: inherit;
  }
  .projectbox::before,
  .projectbox::after {
    display: none;
  }
  .projectbox:not(:first-of-type) {
    margin-top: 5px;
  }
  .projectbox ul {
    position: static;
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
  .projectbox li:last-child {
    width: auto;
    padding: 0;
  }
  .button i {
    font-size: 2.5em;
    color: #333;
  }
  .row {
    margin: 0;
  }
  .row,
  .row > .column {
    padding: 0;
  }
  .navTop {
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 1;
    margin: 0;
    padding: 5px 0;
    position: fixed; /* Make it stick/fixed */
    top: 0; /* Stay on top */
    width: 100%; /* Full width */
    transition: top 0.6s; /* Transition effect when sliding down (and up) */
  }
  .navbar--hidden {
    top: -50px;
  }
  .navTop .icon {
    display: block;
    color: #ff7214;
    font-weight: 100;
  }
  .navTop .text {
    display: none;
  }
  /* Responsive layout - makes the two columns stack on top of each other instead of next to each other */

  .column {
    width: 100%;
  }
  .collab {
    display: none;
  }
  .contactNav {
    display: none;
  }
  form {
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    padding: 5%;
  }
  input,
  textarea,
  button {
    display: block;
    width: 100%;
  }
}

* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
  font-size: calc(14px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
  background-color: silver;
  font-family: "Open Sans", sans-serif;
}
body {
  background-image: url(/portfolio/static/media/rose-blue.e809a575.svg);
  background-size: auto;
  background-attachment: fixed;
  background-repeat: repeat;
  width: 100%;
  min-height: 100vh;
}
/* border page */
#top,
#bottom,
#left,
#right {
  background: whitesmoke;
  position: fixed;
  z-index: 1;
}
#left,
#right {
  top: 0;
  bottom: 0;
  width: 5px;
}
#left {
  left: 0;
}
#right {
  right: 0;
}

#top,
#bottom {
  left: 0;
  right: 0;
  height: 5px;
}
#top {
  top: 0;
}
#bottom {
  bottom: 0;
}
/* border page end*/

section:not(.home) {
  max-width: 1000px;
  margin: auto;
  padding: 70px 50px 50px 50px;
  background-color: rgba(0, 0, 0, 0.4);
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
h1 {
  font-size: 3rem;
  font-weight: bolder;
  font-family: "Julius Sans One", sans-serif;
  color: #ff7214;
  text-shadow: 5px 5px 0 rgba(255, 0, 180, 0.5);
  border-bottom: 1px solid white;
  word-break: break-all;
  padding-bottom: 50px;
  margin-bottom: 50px;
}
h3,
article,
p {
  color: white;
}
/* animation */

.puff-in-center {
  -webkit-animation: puff-in-center 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55)
    both;
  animation: puff-in-center 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55) both;
}
/* keyframes */

@-webkit-keyframes puff-in-center {
  0% {
    -webkit-transform: scale(2);
    transform: scale(2);
    -webkit-filter: blur(4px);
    filter: blur(4px);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}
@keyframes puff-in-center {
  0% {
    -webkit-transform: scale(2);
    transform: scale(2);
    -webkit-filter: blur(4px);
    filter: blur(4px);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}

@media screen and (max-width: 768px) {
  body {
    background-size: cover;
    padding: 0;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  #top,
  #bottom,
  #left,
  #right {
    display: none;
  }
  section:not(.home) {
    margin: 0 auto;
    padding: 50px 0;
    background-color: transparent;
    box-shadow: none;
  }
  h1 {
    margin-left: 5%;
  }
  article,
  p {
    color: whitesmoke;
    font-weight: bolder;
    font-size: 1.2em;
    text-align: left;
    margin: 0 5%;
  }
  h2 {
    text-align: center;
    padding-top: 5%;
  }
}

